import React from 'react';
import './Card.scss';
import ReactCardFlip from 'react-card-flip';
import Tilt from 'react-parallax-tilt';

const Card = ( { handleClick, content, id, isFlipped, value, handleDoubleClick } ) => {
    
    
    return (
        <Tilt>
            <ReactCardFlip isFlipped={isFlipped} flipDirection='horizontal' flipSpeedBackToBack={0.4} flipSpeedBackToFront={0.4} >

                <div
                    className='card'
                    id={id} 
                    onClick={() => handleClick(id)}
                    value={value}
                >
                </div>

                <div
                    className='card-translation'
                    id={id}
                    onDoubleClick={() => handleDoubleClick(id)}
                    value={value}
                >    
                    {content}
                </div>

            </ReactCardFlip>
        </Tilt>
    );
};

export default Card;
